import React from "react";
import styled from "styled-components";
import { AiOutlineClose } from "react-icons/ai";
import Button from "../components/button";
import { Link } from "react-router-dom";

const OfferBackground = styled.div`
  overflow: hidden;
  z-index: 150;
  width: 100%;
  background-color: rgba(0, 0, 0, 0.3);
  height: 100vh;
  position: relative;
`;

const PopupContainer = styled.div`
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 1000px;
  height: 300px;
  box-shadow: 0 5px 16px rgba(0, 0, 0, 0.2);
  background: #232b2b;
  color: #fff;
  padding: 3rem;
  z-index: 100;
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  .offer-heading {
    margin-top:2.5%;  
    font-family: "Montez";
    font-size: 3rem;
    color: #fccb13;

    @media (max-width: 768px) {
      padding: 0;
      font-size: 2.5rem;
    }
  }

  .offer-button {
    text-decoration: none;
    color: #fff;
    

    &:hover {
      color: #fccb13;
    }
  }

  @media (max-width: 768px) {
    width: 600px;
    height: 700px;
    padding: 1.5rem;
  }
  @media (max-width: 480px) {
    width: 370px;
    height: 600px;
    padding: 0.4;
  }
`;
const OfferPopupWrapper = styled.div`
  display: flex;
  justify-content: space-around;
  align-items: center;
  padding-bottom: 2rem;

  @media (max-width: 768px) {
    padding: 0;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
`;

const LeftSection = styled.div`
  width: 100%;
`;
const RightSection = styled.div`
  width: 100%;
`;

const CloseIcon = styled.div`
  position: absolute;
  top: 3%;
  right: 3%;
  color: #fff;
  font-size: 1.5rem;
  cursor: pointer;
`;

const ClosedPopup = ({ open, setOpen, setfix }) => {

  const handlefix =() => {
    setOpen(false);
    setfix(false);
  }
   
    return (
      <>
        {open ? (
          <OfferBackground>
            <PopupContainer>
              <h1 className="offer-heading text-center">"Ravintola Mount Nepal"</h1>
              <OfferPopupWrapper>
                <CloseIcon>
                  <AiOutlineClose onClick={() => handlefix()} />
                </CloseIcon>
                <LeftSection>
                We are closed today. Sorry for the inconvinence
              </LeftSection>
              
              </OfferPopupWrapper>
            </PopupContainer>
          </OfferBackground>
        ) : null}
      </>
      
   
    );
  }
  
  export default ClosedPopup;