import React, { useState } from "react";
import "./Counter.css";
import styled from "styled-components";

const ChangeLocationSection = styled.div`
  .change-location {
    height: 160px;
    width: 40px;
    text-decoration: none;
    color: #fff;
    font-size: 14px;
    background-color: #393939;
    z-index: 15;
    position: fixed;
    left: 0;
    top: 20%;
    border-radius: 2px;
    display: flex;
    justify-content: center;
    align-items: center;
    opacity: 0.9;
    transition: transform 250ms;
    cursor: pointer;

    &:hover {
      color: #fff;
      transform: translateY(-2px);
    }
  }
  .change-location p {
    margin: 0;
    writing-mode: vertical-lr;
    text-orientation: mixed;
    opacity: 1;
  }

  .navinactive {
    display: none;
  }
`;

const Counter = (props) => {
  const [visible, setVisible] = useState(true);
  return (
    <div className="ComingSoon">
      <div className="display-table">
        <div className="display-table-cell contentContainer">
          <h1 className="title font-white">
            <b>OPENING SOON</b>
          </h1>
          <h1 className="subTitle font-white">
            Our resturant is looking forward to serving you.
          </h1>
          <div className="counting">
            <p className="countDays">{props.days}</p>
            <p className="units">Days</p>
            <p className="countHour">{props.hours}</p>
            <p className="units">Hours</p>
            <p className="countMin">{props.minutes}</p>
            <p className="units">Minutes</p>
            <p className="countSec">{props.seconds}</p>
            <p className="units">Seconds</p>
          </div>
        </div>
      </div>
      <ChangeLocationSection>
        <div className={visible ? "change-location" : "navinactive"}>
          <p onClick={() => window.location.reload(false)}>Change Location</p>
        </div>
      </ChangeLocationSection>
    </div>
  );
};

export default Counter;
