import React, { useState } from "react";
import styled from "styled-components";
import { Link } from "react-router-dom";
import { NavLink } from "react-router-dom"
import navbarItems from "../data/navbarItems";
import Brand from "../assets/images/Picture1.png";
import Menubar from "../assets/svg/menubar1.svg";
import UKFlag from "../assets/svg/uk.svg";
import FinlandFlag from "../assets/svg/finland.svg";
import { useTranslation } from 'react-i18next';
import i18next from 'i18next';

const NavbarContainer = styled.div`
  width: 100%;
  height: 20vh;
  background: transparent;
  color: #fff;
  z-index: 100;
  position: fixed;

  .active {
    background: #000;
    opacity: 0.94;
    height: 15vh;
    transition: .3s ease-in;
  }
  .cusnav {
    height: 20vh;
  }

  @media (max-width: 1024px) {
    .cusnav {
      height: 15vh;
    }
    .active {
      height: 12vh;
    }
  }

  @media (max-width: 768px) {
    .active {
      display: none;
    }
  }
`;
const LangLocSection = styled.div`
  position: absolute;
  color: #fff;
  top: 1%;
  right: 7%;
  display: flex;
  font-size: 0.8rem;

  @media (max-width: 768px) {
    right: 1%;
  }
`;

const LanguageSection = styled.div`
  margin-right: 1.5rem;
  z-index: 100;
 
  span {
    cursor: pointer;
  }
  .uk-flag{
    cursor:pointer;
  }
  img {
    width: 25px;
    height: 15px;
    margin-right: 0.5rem;
    @media (max-width: 375px) {
      width:19px;
      height:14px;

     }
  }

  @media (max-width: 768px) {
    margin-right: 0.5rem;
  }
 
`;
const AddressSection = styled.div`
  .address-section {
    display: flex;

    .faslogo {
      margin-right: 0.5rem;
    }

    .fas-content {
      margin-right: 1rem;
    }
    @media (max-width: 375px) {
     font-size:9px;
    }
  }
`;

