import React from "react";
import MenuBkg from "../assets/images/aboutbkg.jpg";

import ExquisiteCuisine from "../components/ExquisiteCuisine";

import FormPageFlat from "../components/ReservationPage/FlatReservePage";
import SpecialOffer from "../components/SpecialOffer";
import OurMenu from "../components/nished/OurMenu";

const Menu = () => {
  return (
    <div>
      <ExquisiteCuisine
        title="Delicious Cuisine"
        head="DISCOVER THE MENU"
        image={MenuBkg}
      />
      <OurMenu />
      <SpecialOffer />
      <FormPageFlat />
    </div>
  );
};

export default Menu;

