import React from "react";
import styled from "styled-components";
import AlacarteMenuItems from "../data/AlacarteMenuItems";
import AlacarteMenuItems2 from "../data/AlacarteMenuItems2";
import AlacarteSalad from "../data/AlacarteSalad";
import AlacarteSoup from "../data/AlacarteSoup";
// import Divider from "./Divider";
// import MenuItem from "./MenuItem";
import MenuHeader from "./MenuHeader";
import AlacarteMenuComponent from "./AlacarteMenuComponent";
// import Popout from "./Popout";
// import Desert from "../assets/FoodImg/desert.png";

const AlacarteMenuContainer = styled.div`
  overflow: hidden;
  width: 100%;
  height: auto;
  display: flex;
  flex-direction: column;
`;
const StarterSection = styled.div`
  display: flex;
  justify-content: space-between;

  @media (max-width: 480px) {
    flex-direction: column;
    justify-content: center;
  }
`;
const ConcreteSection = styled.div`
  display: flex;
  justify-content: space-between;
  @media (max-width: 480px) {
    flex-direction: column;
    justify-content: center;
  }
`;

const MenuItems = styled.div`
  width: 50%;

  .offer-title {
    overflow: hidden;
    font-family: "Suranna", sans-serif;
    font-size: 2rem;
    color: #000;
    margin: 0;
    line-height: 1.2;
    @media (max-width: 968px) {
      font-size: 2rem;
    }
  }

  @media (max-width: 968px) {
    padding: 0.4rem;
    width: 100%;
    height: 100%;
  }
`;
const ItemHead = styled.div`
  margin: 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding-left: 1rem;
`;

const AlacarteMenu = () => {
  return (
    <AlacarteMenuContainer>
      <StarterSection>
        <MenuItems>
          <ItemHead></ItemHead>
          {AlacarteSoup.map((item, index) => {
            return (
              <AlacarteMenuComponent
                key={index}
                id={item.id}
                reference={item.reference}
                dishname={item.dishname}
                dishitems={item.dishitems}
                price={item.price}
              />
            );
          })}
        </MenuItems>
        <MenuItems>
          <ItemHead></ItemHead>
          {AlacarteSalad.map((item, index) => {
            return (
              <AlacarteMenuComponent
                key={index}
                id={item.id}
                reference={item.reference}
                dishname={item.dishname}
                dishitems={item.dishitems}
                price={item.price}
              />
            );
          })}
        </MenuItems>
      </StarterSection>
      <MenuHeader title="More concrete" name="MAIN DISHES" desc="" />
      <ConcreteSection>
        <MenuItems>
          {AlacarteMenuItems.map((item, index) => {
            return (
              <AlacarteMenuComponent
                key={index}
                dishname={item.dishname}
                dishitems={item.dishitems}
                id={item.id}
                reference={item.reference}
                price={item.price}
              />
            );
          })}
        </MenuItems>
        <MenuItems>
          {AlacarteMenuItems2.map((item, index) => {
            return (
              <AlacarteMenuComponent
                key={index}
                id={item.id}
                reference={item.reference}
                dishname={item.dishname}
                dishitems={item.dishitems}
                price={item.price}
              />
            );
          })}
        </MenuItems>
      </ConcreteSection>
      
    </AlacarteMenuContainer>
  );
};

export default AlacarteMenu;
