import React, { useEffect } from "react";
import styled from "styled-components";

const MenuItemContainer = styled.div`
  overflow: hidden;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  margin-bottom: 1rem;
  width: 90%;

  .dish-price {
    border-top: 1px solid #b48c80;
    border-bottom: 1px solid #b48c80;
    padding: 1rem;
    font-family: "Montez", sans-serif;
    font-size: 2rem;
    color: #b48c80;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  @media (max-width: 1440px) {
    width: 95%;
  }

  @media (max-width: 768px) {
    width: 100%;
    margin-bottom: 0.5rem;
    justify-content: center;
    align-items: center;
  }
`;

const DishImage = styled.img`
  width: 112px;
  height: 100%;
  border-radius: 50%;
  background: linear-gradient(55, 55, 55, 0.7);
`;

const DishContent = styled.div`
  display: flex;
  width: 90%;
  flex-direction: column;
  justify-content: flex-start;
  padding: 0 1rem;

  .dish-name {
    margin: 0;
    text-transform: uppercase;
    font-size: 1.2rem;
    padding-bottom: 0.3rem;

    @media (max-width: 768px) {
      font-size: 14px;
    }
  }
  .dish-items {
    margin: 0;

    font-size: 14px;

    @media (max-width: 768px) {
      display: none;
    }
  }
`;

const MenuItemOffer = (props) => {
  return (
    <MenuItemContainer key={props.id}>
      <DishImage src={props.image}></DishImage>
      <DishContent>
        <p className="dish-name">{props.dishname}</p>
        <p className="dish-items">{props.dishitems[0]}</p>
        <p className="dish-items">{props.dishitems[1]}</p>
      </DishContent>
      <p className="dish-price">{props.price}</p>
    </MenuItemContainer>
  );
};

export default MenuItemOffer;
