import React from "react";
import styled from "styled-components";
import {
  specialOfferItemLeft,
  specialOfferItemRight,
} from "../data/CustomersFavourite";
import MenuItem from "./MenuItem";
import { Container } from "@material-ui/core";

const BackgroundContainer = styled.div`
  width: 100%;
  background: linear-gradient(290deg, #fbc2eb, #a6c1ee);

`

const SpecialOfferContainer = styled.div`
  overflow: hidden;
  min-height: 65vh;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  padding: 0 1rem;
  .sticker {
    font-family: "Suranna";
    font-size: 2.5rem;
    color: #c70039;
    padding-bottom: 2rem;
  }

  @media (max-width: 768px) {
    height: auto;
    padding: 1rem;
  }
`;

const FoodContainer = styled.div`
  display: flex;

  @media (max-width: 768px) {
    flex-direction: column;
  }
`;

const LeftSection = styled.div``;
const RightSection = styled.div``;

const SpecialOffer = () => {
  return (
    <BackgroundContainer>
    <Container maxWidth="xl">
      <SpecialOfferContainer>
        <h2 className="sticker">
          Customer's Favourite
          <hr />
        </h2>
        <FoodContainer>
          <LeftSection>
            {specialOfferItemLeft.map((item, index) => {
              return (
                <MenuItem
                  key={index}
                  id={item.id}
                  reference={item.reference}
                  image={item.image}
                  dishname={item.dishname}
                  dishitems={item.dishitems}
                  price={item.price}
                />
              );
            })}
          </LeftSection>
          <RightSection>
            {specialOfferItemRight.map((item, index) => {
              return (
                <MenuItem
                  key={index}
                  id={item.id}
                  reference={item.reference}
                  image={item.image}
                  dishname={item.dishname}
                  dishitems={item.dishitems}
                  price={item.price}
                />
              );
            })}
          </RightSection>
        </FoodContainer>
      </SpecialOfferContainer>
    </Container>
    </BackgroundContainer>
  );
};

export default SpecialOffer;
