import React from "react";
import renderer from "./components/Countdown/Count";
import Countdown from "react-countdown";

function SecondResturant() {
  return (
    <div>
      <Countdown date={new Date('2021-04-01T11:00:00')} renderer={renderer} zeroPadDays={2}/>
    </div>
  );
}

export default SecondResturant;
