import React, { useEffect, useState } from "react";
import OurStory from "../components/OurStory";
import ExquisiteCuisine from "../components/ExquisiteCuisine";
import Mountnepal1 from "../assets/images/Mountnepal1.jpg";
import Cuisine3 from "../assets/images/cuisine3.jpg";
import FooterBkg from "../assets/images/footerbkg.jpg";
import HomeMenu from "../components/HomeMenu";
import LandingPage from "../components/LandingPage";
import FormPage from "../components/ReservationPage/FormPage";
import OfferPopup from "../components/OfferPopup";
import Modal from "@material-ui/core/Modal";
import { makeStyles } from "@material-ui/core/styles";
import ExquisiteCuisineMin from "../components/ExquisiteCuisineMin";
import ExquisiteCuisineBtn from "../components/ExquisiteCuisineBtn";
import axios from "axios";
import ClosedPopup from "./ClosedPopup";
// function rand() {
//   return Math.round(Math.random() * 20) - 10;
// }

function getModalStyle() {
  const top = 50;
  const left = 50;

  return {
    top: `${top}%`,
    left: `${left}%`,
    transform: `translate(-${top}%, -${left}%)`,
  };
}
const useStyles = makeStyles((theme) => ({
  paper: {
    position: "absolute",
  },
}));

const HomePage = () => {
  const [fix, setfix] = React.useState(false);
  const [open, setOpen] = React.useState(false);
  const [close, setClose] = React.useState(false);

  const url = "http://localhost:4000/offer";

  const fetchData = () => {
    axios.get(url).then((response) => {
      console.log(response.data[0].status);
      if (response.data[0].status === "true") {
        setOpen(true);
        //setClose(false);
        setfix(true);
      } else if (response.data[0].status === "off") {
        setClose(true);
        setfix(true);
        // setOpen(false);
      } else {
        setOpen(false);
        setClose(false);
        setfix(false);
      }
    });
  };

  const classes = useStyles();

  const [modalStyle] = React.useState(getModalStyle);
  const handleClose = () => {
    setOpen(false);
    setClose(false);
  };

  useEffect(() => {
    fetchData();
  }, []);

  return (
    <>
      <Modal open={fix} onClose={handleClose}>
        <div style={modalStyle} className={classes.paper}>
          <OfferPopup open={open} setOpen={setOpen} setfix={setfix} />
          <ClosedPopup open={close} setOpen={setClose} setfix={setfix} />
        </div>
      </Modal>

      <LandingPage />
      <OurStory />
      <ExquisiteCuisineBtn
        title="Namaste"
        head="A WARM WELCOME TO RAVINTOLA MOUNT NEPAL"
        image={Mountnepal1}
        divider={true}
        button="true"
      />
      <HomeMenu />
      <ExquisiteCuisineMin
        title="Visit us at Ravintola Mount Nepal"
        head="ENJOYABLE & COMFORTABLE"
        image={FooterBkg}
        divider={true}
      />
      <FormPage />
    </>
  );
};

export default HomePage;
