import React from "react";
import ContactBkg from "../assets/images/home1.jpg";
import ExquisiteCuisine from "../components/ExquisiteCuisine";
import ContactUs from "../components/ContactUsForm/ContactUs";
import VisitUs from "../components/VisitUsPage/VisitUs";

const Contact = () => {
  return (
    <div>
      <ExquisiteCuisine
        title="Contact us"
        head="SEND US A MESSAGE"
        image={ContactBkg}
      />
      <ContactUs />
      <VisitUs />
    </div>
  );
};

export default Contact;
