import { Modal, Button } from "react-bootstrap";
import React, { useState } from "react";
import { MDBBtn, MDBIcon } from "mdbreact";
import { Fragment } from "react";
import LocalDiningIcon from "@material-ui/icons/LocalDining";
import TabMenu from "./TabMenu";
import FriFood from "./data/FriFood";
import MonFood from "./data/MonFood";
import TueFood from "./data/TueFood";
import WedFood from "./data/WedFood";
import ThuFood from "./data/ThuFood";
import { Tab, Tabs, TabList, TabPanel } from "react-tabs";
import "react-tabs/style/react-tabs.css";
import "./ourMenu.css";
import styled from "styled-components";

import AsianFood from "./data/AsianFood";
import SideDishes from "./data/SideDishes";
import ChickenDishes from "./data/ChickenDishes";
import GroupMenu from "./data/GroupMenu";
import Starter from "./data/Starter";
import LambDishes from "./data/LambDishes";
import Noodles from "./data/Noodles";
import SeaFood from "./data/SeaFood";
import TandooriDishesChicken from "./data/TandooriDishesChicken";
import ThaliDishes from "./data/ThaliDishes";
import VegetableDishes from "./data/VegetableDishes";
import Desserts from "./data/Desserts";
import TandooriDishesLamb from "./data/TandooriDishesLamb";
import Beverages from "./data/Beverages";
import ChildrenMenu from "./data/ChildrenMenu";
import Momo from "./data/Momo";
import DrinkTabMenu from "./DrinkTabMenu";

const MenuPopupContainer = styled.div`
  min-height: 40vh;
  height: auto;
`;

function OurMenu() {
  const [show, setShow] = useState(false);
  const handleShow = () => setShow(!show);
  const handleClose = () => setShow(false);

  // for alacarte
  const [alacarteShow, setAlacarteShow] = useState(false);
  const handleAlacarteShow = () => setAlacarteShow(!alacarteShow);
  const handleAlacarteClose = () => setAlacarteShow(false);

  return (
    <div className="ourMenuContainer">
      <MenuPopupContainer className="containerMenu">
        <div className="menu-heading">OUR MENU</div>
        <div className="design">
          _______________
          <LocalDiningIcon />
          _______________
        </div>
        <Fragment>
          <MDBBtn gradient="blue" onClick={handleAlacarteShow}>
            <MDBIcon icon="utensil-spoon" className="mr-1" /> A-LA-CARTE
          </MDBBtn>
          <MDBBtn gradient="aqua" onClick={handleShow}>
            LUNCH <MDBIcon icon="utensils" className="ml-1" />
          </MDBBtn>
        </Fragment>
        <div>
          {show ? (
            <Modal
              size="lg"
              show={show}
              onHide={handleClose}
              animation={false}
              scrollable={true}
              dialogClassName="modal-150w"
            >
              <Modal.Header closeButton>
                <Modal.Title>
                  LUNCH{" "}
                  <div className="lunchPopupHeading">MON-FRI: 11:00-15:00</div>
                </Modal.Title>
              </Modal.Header>
              <Modal.Body>
                <Tabs>
                  <TabList className="tabList">
                    <Tab>MON</Tab>
                    <Tab>TUE</Tab>
                    <Tab>WED</Tab>
                    <Tab>THU</Tab>
                    <Tab>FRI</Tab>
                  </TabList>

                  <TabPanel className="tabPanel">
                    <TabMenu className="tabMenu" Food={MonFood} />
                  </TabPanel>
                  <TabPanel>
                    <TabMenu Food={TueFood} />
                  </TabPanel>
                  <TabPanel>
                    <TabMenu Food={WedFood} />
                  </TabPanel>
                  <TabPanel>
                    <TabMenu Food={ThuFood} />
                  </TabPanel>
                  <TabPanel>
                    <TabMenu Food={FriFood} />
                  </TabPanel>
                </Tabs>
              </Modal.Body>
              <Modal.Footer>
                <Button variant="dark" onClick={handleClose}>
                  Done
                </Button>
              </Modal.Footer>
            </Modal>
          ) : null}
        </div>

        <div>
          {alacarteShow ? (
            <Modal
              size="lg"
              show={alacarteShow}
              onHide={handleAlacarteClose}
              animation={false}
              scrollable={true}
              dialogClassName="modal-20w"
              aria-labelledby="example-custom-modal-styling-title"
            >
              <Modal.Header closeButton>
                <Modal.Title>A-LA-CARTE</Modal.Title>
              </Modal.Header>
              <Modal.Body>
                <Tabs className="tabs">
                  <TabList className="tabList">
                    <Tab>Starter</Tab>
                    <Tab>Vegetable Dishes</Tab>
                    <Tab>Chicken Dishes</Tab>
                    <Tab>Lamb Dishes</Tab>
                    <Tab>Thali Dishes</Tab>
                    <Tab>Tandoori Dishes Chicken</Tab>
                    <Tab>Tandoori Dishes Lamb</Tab>
                    <Tab>Sea Food</Tab>
                    <Tab>Noodles</Tab>
                    <Tab>Momo</Tab>
                    <Tab>Asian Food</Tab>
                    <Tab>Children Menu</Tab>
                    <Tab>Side Dishes</Tab>
                    <Tab>Group Menu</Tab>
                    <Tab>Desserts</Tab>
                    <Tab>Beverages</Tab>
                  </TabList>

                  <TabPanel>
                    <TabMenu Food={Starter} />
                  </TabPanel>
                  <TabPanel>
                    <TabMenu Food={VegetableDishes} />
                  </TabPanel>
                  <TabPanel>
                    <TabMenu Food={ChickenDishes} />
                  </TabPanel>
                  <TabPanel>
                    <TabMenu Food={LambDishes} />
                  </TabPanel>
                  <TabPanel>
                    <TabMenu Food={ThaliDishes} />
                  </TabPanel>
                  <TabPanel>
                    <TabMenu Food={TandooriDishesChicken} />
                  </TabPanel>
                  <TabPanel>
                    <TabMenu Food={TandooriDishesLamb} />
                  </TabPanel>

                  <TabPanel>
                    <TabMenu Food={SeaFood} />
                  </TabPanel>
                  <TabPanel>
                    <TabMenu Food={Noodles} />
                  </TabPanel>
                  <TabPanel>
                    <TabMenu Food={Momo} />
                  </TabPanel>
                  <TabPanel>
                    <TabMenu Food={AsianFood} />
                  </TabPanel>
                  <TabPanel>
                    <TabMenu Food={ChildrenMenu} />
                  </TabPanel>
                  <TabPanel>
                    <TabMenu Food={SideDishes} />
                  </TabPanel>

                  <TabPanel>
                    <TabMenu Food={GroupMenu} />
                  </TabPanel>

                  <TabPanel>
                    <TabMenu Food={Desserts} />
                  </TabPanel>
                  <TabPanel>
                    <DrinkTabMenu Food={Beverages} />
                  </TabPanel>
                </Tabs>
              </Modal.Body>

              <Modal.Footer>
                <Button variant="dark" onClick={handleAlacarteClose}>
                  Done
                </Button>
              </Modal.Footer>
            </Modal>
          ) : null}
        </div>
      </MenuPopupContainer>
    </div>
  );
}
export default OurMenu;

